<template>
  <div>
    <base-dialog
      :show="!!error"
      title="An error occurred!"
      @close="handleError"
    >
      <p>{{ error }}</p>
    </base-dialog>
    <section>
      <base-card>
        <header>
          <h2>Request Recevies</h2>
        </header>
        <base-spinner v-if="isLoading"></base-spinner>
        <ul v-else-if="hasRequests && !isLoading">
          <request-item
            v-for="req in recivedRequests"
            :key="req.id"
            :email="req.userEmail"
            :message="req.message"
          ></request-item>
        </ul>
        <h3 v-else>You haven`t reveived any requests</h3>
      </base-card>
    </section>
  </div>
</template>

<script>
import RequestItem from '../../components/requests/RequestItem.vue'

export default {
  components: {
    RequestItem
  },
  data () {
    return {
      isLoading: false,
      error: null
    }
  },
  computed: {
    recivedRequests () {
      return this.$store.getters['requests/requests']
    },
    hasRequests () {
      return this.$store.getters['requests/hasRequests']
    }
  },
  created () {
    this.loadRequests()
  },
  methods: {
    async loadRequests () {
      this.isLoading = true
      try {
        await this.$store.dispatch('requests/fetchRequests')
      } catch (error) {
        this.error = error.message || 'something failed'
      }

      this.isLoading = false
    },
    handleError () {
      this.error = null
    }
  }
}
</script>

<style lang="scss" scoped>
header {
  text-align: center;
}

ul {
  list-style: none;
  margin: 2rem auto;
  padding: 0;
  max-width: 30rem;
}

h3 {
  text-align: center;
}
</style>
